'use strict';(function () {

    var modalOpen = document.getElementById('modal-open');
    modalOpen.addEventListener('click', function () {
        reducer('MODAL OPEN');
    });

    var modalClose = document.getElementById('modal-close');

    var sendHome = function sendHome() {return window.location.assign('' + window.location.origin);};

    var reducer = function reducer(action) {
        switch (action) {
            case 'MODAL OPEN':
                console.log('Modal Open');
                videoPlayer();
                break;
            case 'MODAL CLOSE':
                console.log('Modal Closed');
                break;
            case 'SEND HOME':
                sendHome();
                break;}

    };

    var videoPlayer = function videoPlayer() {
        /* VIMEO MODAL */
        var options = {
            id: 187136326,
            loop: false,
            autoplay: true,
            byline: false,
            portrait: false,
            title: false };


        var playerContainer = document.getElementById('vimeo-player');

        var player = new Vimeo.Player(playerContainer, options);

        player.setVolume(0.5);

        player.on('play', function () {
            console.log('played the video!');
        });

        player.ready().then(function () {
            console.log('[v-player] Video ready');
        });

        player.on('ended', function () {
            console.log('video ended');
            // close video when ended
            reducer('SEND HOME');
        });

        modalClose.addEventListener('click', function () {
            reducer('MODAL CLOSE');
            player.unload();
        });
    };

})();
